// Error dialog for EPQ (adult version).
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  Button,
} from "@material-ui/core";
import React from "react";

interface Props {
  isOpen: boolean;
  handleClose: () => void;
  goToLogInPage: () => void;
  age?: null | number;
  gender?: null | string;
}

export const EnterGenderAndAgeDialog = ({
  isOpen,
  handleClose,
  goToLogInPage,
  age,
  gender,
}: Props) => {
  return (
    <Dialog open={isOpen} onClose={handleClose}>
      <DialogTitle>
        {!age || (age >= 16 && !gender)
          ? ""
          : age >= 7 && age < 16
          ? ""
          : ""}
      </DialogTitle>
      <DialogContent>
        <DialogContentText>
          {(!age || !gender) && ""}
          {age !== undefined && age !== null && age < 16
            ? "16"
            : null}
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button
          onClick={!age || age >= 16 ? goToLogInPage : handleClose}
          color="primary"
          autoFocus
        >
          
        </Button>
      </DialogActions>
    </Dialog>
  );
};
