// Error dialog for EPQ (children version).
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  Button,
} from "@material-ui/core";
import React from "react";

interface Props {
  isOpen: boolean;
  handleClose: () => void;
  goToLogInPage: () => void;
  age?: null | number;
  gender?: null | string;
}

export const ChildrenEnterGenderAndAgeDialog = ({
  isOpen,
  handleClose,
  goToLogInPage,
  age,
  gender,
}: Props) => {
  return (
    <Dialog open={isOpen} onClose={handleClose}>
      <DialogTitle>
        {!age || (age < 16 && age > 7 && !gender)
          ? ""
          : age >= 16
          ? ""
          : ""}
      </DialogTitle>
      <DialogContent>
        <DialogContentText>
          {(!age || !gender) && ""}
          {age !== undefined && age !== null && (age >= 16 || age < 7)
            ? "715"
            : null}
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button
          onClick={
            !age || !(age >= 16 || age < 7) ? goToLogInPage : handleClose
          }
          color="primary"
          autoFocus
        >
          
        </Button>
      </DialogActions>
    </Dialog>
  );
};
