// Error dialog for EPQ (children version).
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  Button,
} from "@material-ui/core";
import React from "react";

interface Props {
  isOpen: boolean;
  handleClose: () => void;
  goToLogInPage: () => void;
  gender?: null | string;
}

export const PsssGenderDialog = ({
  isOpen,
  handleClose,
  goToLogInPage,
  gender,
}: Props) => {
  return (
    <Dialog open={isOpen} onClose={handleClose}>
      <DialogTitle></DialogTitle>
      <DialogContent>
        <DialogContentText></DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button onClick={goToLogInPage} color="primary" autoFocus>
          
        </Button>
      </DialogActions>
    </Dialog>
  );
};
